@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Open+Sans:ital,wght@0,300;0,400;0,600;1,400&display=swap);
.App{display:flex;justify-content:center;font-family:'Open Sans', sans-serif;box-sizing:border-box;margin:0;padding:0}.App h1{color:#ff7b59;background-color:#02122c;font-family:'Bebas Neue', cursive;font-size:50px;width:100%;display:flex;justify-content:center}@media (max-width: 600px){.App h1{font-size:40px}}.App .btn{height:40px;width:100px;border-radius:100px;border:none;cursor:pointer;font-family:'Bebas Neue', cursive;font-size:20px}.App .li{background-color:#d6fffc}.App .su{background-color:#ffcf8b}.App .lo{background-color:#ffa9b8}.App .Login{width:50vw;display:flex;flex-direction:column;align-items:center}@media (max-width: 600px){.App .Login{width:100vw}}.App .Login .from{height:110px;display:flex;flex-direction:column;justify-content:space-around}.App .Login .from input{height:25px;width:250px;border-radius:10px;font-family:'Open Sans', sans-serif;padding-left:10px}.App .Login .submit{margin:30px;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.App .Home{width:50vw;display:flex;flex-direction:column;align-items:center}@media (max-width: 600px){.App .Home{width:100vw}}

